<template>
  <div id="main">
    <h2>Datanorm</h2>
    <b-form @submit="onSubmit">
      <b-form-group
        id="input-group-1"
        label="Datanorm datanorm.001 Input name:"
        label-for="input-1"
      >
        <b-form-input :state="nameState" v-model="name" placeholder></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2" label="Datanorm File:" label-for="file">
        <b-form-file
          v-model="file"
          :state="Boolean(file)"
          placeholder="Choose a file or drop it here..."
          drop-placeholder="Drop file here..."
        ></b-form-file>
      </b-form-group>

      <b-button type="submit" variant="primary">Submit</b-button>
    </b-form>

    <h2>Current Vendors</h2>
    <div class="row pl-2 pb-1" v-for="row in vendorList" v-bind:key="row._id">
      {{ row.vendorId }} ## {{ row.file }} ## {{ row.uploadName }} ## {{ row.articles.length }} ##
      <b-button variant="danger" @click="onDelete(row._id)" :disabled="true">Delete</b-button>
    </div>

    <h2>Search</h2>
    <b-form class="form-inline" @submit="onSearch">
      <b-form-input class="col-8" :state="queryState" v-model="searchQuery"></b-form-input>
      <b-button class="col-2" type="submit" variant="primary">Submit</b-button>
    </b-form>
    <div class="row pl-2 pb-1" v-for="row in searchResults" v-bind:key="row._id">
      {{ row.articleId }} ## {{ row.description1 }} ## {{ row.description2 }} ## {{ row.price }} 
    </div>

    <ArticleSearch
      v-on:add-item="onAddItemCallback($event)"
    ></ArticleSearch>
  </div>
</template>

<script>
import axios from "axios";
import ArticleSearch from './ArticleSearch';
//import router from "../router";

export default {
  name: "DatanormView",
  components: {
    ArticleSearch,
  },

  data() {
    return {
      name: "test",
      file: null,
      vendorList: {},

      searchQuery: "",
      searchResults: [],
    };
  },
  computed: {
    nameState() {
      return this.name.length > 0 ? true : false;
    },
    queryState() {
      return this.searchQuery.length > 3 ? true : false;
    }
  },
  methods: {
    onAddItemCallback(evt) {
      this.$log.debug(evt)
    },

    onSubmit(evt) {
      evt.preventDefault();

      let formData = new FormData();
      formData.append("name", this.name);
      formData.append("file", this.file);

      axios
        .post("/api/vendors/uploadDatanorm", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(response => {
          this.$log.debug("Upload done");
          this.$log.debug(response.data);
          this.$bvToast.toast("Yipii", {
            title: `Upload complete`,
            variant: "success",
            solid: true
          });
        })
        .catch(errors => {
          this.$log.error("Cannot upload " + errors);
          this.showLoginFailedAlert = true;
        });
    },

    onDelete(articleId) {
      console.log(articleId)
      /*
      axios
        .delete("/api/vendors/" + articleId)
        .then(response => {
          this.$log.info("delete okay " + response);
          this.$bvToast.toast("ok", {
            title: `Delete`,
            solid: true
          });
          this.refreshTables();
        })
        .catch(errors => {
          this.$log.error("get users fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });
        */
    },

    onSearch(evt) {
      evt.preventDefault();

      console.log("onSearch");

      axios
        .get("/api/vendors/search/" + encodeURIComponent(this.searchQuery))
        .then(response => {
          this.$log.info("search okay ");
          this.$log.debug(response.data.articles);
          this.searchResults = response.data.articles;

          this.$bvToast.toast("ok", {
            title: `Delete`,
            solid: true
          });
        })
        .catch(errors => {
          this.$log.error("fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });
    },

    refreshTables() {
      axios
        .get("/api/vendors/")
        .then(response => {
          this.$log.debug("Vendors");
          this.$log.debug(response.data);
          this.vendorList = response.data.vendors;
        })
        .catch(errors => {
          this.$log.error("get users fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });
    }
  },
  mounted() {
    this.refreshTables();
  }
};
</script>

<style scoped>
#main {
  width: 85%;
  margin: auto;
}
</style>
