<template>
  <div id="main">
    <h2>Tasklist</h2>
    <b-table
      :items="getActiveTasks"
      :fields="activefields"
      :busy="isBusy"
      striped
      hover
      responsive="sm"
    >
      <template v-slot:cell(actions)="row">
        <b-button size="sm" @click="onOpen(row.item)" class="mr-2" variant="success">Open</b-button>
        <b-button size="sm" @click="onArchiveChange(row.item, true)" class="mr-2">Archive</b-button>
        <b-button
          size="sm"
          @click="onDeleteTask(row.item, $evt)"
          class="mr-2"
          variant="danger"
        >Delete</b-button>
      </template>
    </b-table>
    <b-button variant="primary" size="sm" @click="showModal=true">Create Task</b-button>
    <br />
    <br />
    <br />
    <br />
    <b-button v-b-toggle.collapse-1>Archive</b-button>
    <b-collapse id="collapse-1" class="mt-2">
      <b-card fluid>
        <p class="col-xs-1" align="center">
          <b-table
            class="table"
            align="center"
            :items="getInactiveTasks"
            :fields="archivefields"
            striped
            hover
            responsive="sm"
          >
            <template v-slot:cell(actions)="row">
              <b-button size="sm" @click="onOpen(row.item)" class="mr-2" variant="success">Open</b-button>
              <b-button size="sm" @click="onArchiveChange(row.item, true)" class="mr-2">Archive</b-button>
              <b-button
                size="sm"
                @click="onDeleteTask(row.item, $evt)"
                class="mr-2"
                variant="danger"
              >Delete</b-button>
            </template>
          </b-table>
        </p>
      </b-card>
    </b-collapse>

    <!-- ONLY MODAL -->
    <b-modal v-model="showModal" title="Create Task">
      <b-container fluid>
        <b-form @submit="onCreateTask">
          <b-form-input v-model="createTaskName" type="text" placeholder="Taskname"></b-form-input>
          <b-button type="submit" variant="primary" class="text-center">Submit</b-button>
        </b-form>
      </b-container>

      <template v-slot:modal-footer>
        <div></div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
import router from "../router";

export default {
  name: "TaskList",
  data() {
    return {
      //create model
      showModal: false,
      createTaskName: "",

      //table stuff
      isBusy: false,

      activefields: [
        { key: "taskName", label: "Task Name" },
        { key: "assignee", label: "Assignee" },
        { key: "status", label: "Status" },
        { key: "actions", label: "Actions" }
      ],
      archivefields: [
        { key: "taskName", label: "Task Name" },
        { key: "actions", label: "Actions" }
      ],
      tasks: []
    };
  },
  computed: {
    getActiveTasks() {
      return this.tasks.filter(task => !task.isArchived);
    },
    getInactiveTasks() {
      return this.tasks.filter(task => task.isArchived);
    }
  },
  methods: {
    onOpen(item) {
      router.push({ name: "TaskView", params: { taskId: item._id } });
    },

    onArchiveChange(item, isArchived) {
      item.isArchived = isArchived;
      this.$log.error(this.tasks);

      const data = {
        task: item
      };

      axios
        .post("/api/tasks/" + item._id, data)
        .then(response => {
          this.$log.debug("save okay " + response);
          this.$bvToast.toast("Everything is good", {
            title: `Task saved`,
            solid: true
          });

          this.refreshTable();
        })
        .catch(errors => {
          this.$log.error("update task fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });

      //this.refreshTable();
    },

    onCreateTask(evt) {
      evt.preventDefault();

      const data = {
        taskName: this.createTaskName
      };
      axios
        .post("/api/tasks/", data)
        .then(response => {
          this.$log.debug("create okay " + response.status);
          this.showModal = false;
          this.createTaskName = "";

          this.refreshTable();
        })
        .catch(errors => {
          this.$log.error("create task fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });

      
    },

    onDeleteTask(item, event) {
      axios
        .delete("/api/tasks/" + item._id)
        .then(response => {
          this.$log.debug("delete okay " + response.status);

          this.refreshTable();
        })
        .catch(errors => {
          this.$log.error("delete task fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });

      this.refreshTable();
      event.target.blur();
    },

    refreshTable() {
      this.isBusy = true;

      axios
        .get("/api/tasks/")
        .then(response => {
          this.$log.debug("Tasks");
          this.$log.debug(response.data);
          this.tasks = response.data.tasks;
        })
        .catch(errors => {
          this.$log.error("get taskview fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });

      this.isBusy = false;
    }
  },
  mounted() {
    this.refreshTable();
  }
};
</script>

<style scoped>
#main {
  width: 95%;
  margin: auto;
}
.table {
  max-width: 400px;
}
</style>
