<template>
  <div id="main">
    <h2 class="text-center">TaskView</h2>
    <b-form @submit="onSubmit">
      <b-form-group label="Task Name" label-for="taskInput">
        <b-form-input id="taskInput" v-model="taskObj.taskName" type="text"></b-form-input>
      </b-form-group>

      <!--<b-form-input v-model="tennantId" type="text"></b-form-input>-->
      <b>Items</b>
      <div class="row">
        <div class="col-3">Article Id</div>
        <div class="col-3">Description</div>
        <div class="col-3">Amount</div>
        <div class="col-3">Vendor</div>
      </div>
      <div
        class="row pl-2 pb-1"
        v-for="(row, index) in taskObj.items"
        v-bind:key="row._id"
        v-bind:index="index"
      >
        <b-form-input class="col-3 addPadding" id="articleId" v-model="row.articleId" type="text"></b-form-input>
        <b-form-input class="col-3" id="description" v-model="row.description" type="text"></b-form-input>
        <b-form-input class="col-3" id="amount" v-model="row.amount" type="text"></b-form-input>
        <b-form-input class="col-2" id="vendor" v-model="row.vendor" type="text"></b-form-input>
        <b-button class="col-1" @click="onDeleteArticle(index)">X</b-button>
      </div>

      <b-button variant="primary" @click="onAddArticle">Add Item</b-button>
      <b-button variant="primary ml-2" @click="showSearchModal = true">Search Item</b-button>

      <br />
      <b>Work Time Stuff</b>
      <div class="row">
        <div class="col-4">Worker</div>
        <div class="col-4">Worker Time</div>
      </div>
      <div
        class="row pl-2 pb-1"
        v-for="(row, index) in taskObj.workTime"
        v-bind:key="row._id"
        v-bind:index="index"
      >
        <b-form-input class="col-4" id="worker" v-model="row.worker" type="text"></b-form-input>
        <b-form-input class="col-4" id="time" v-model="row.time" type="text"></b-form-input>
        <b-button class="col-1 ml-2" @click="onDeleteWorkTimeItem(index)">X</b-button>
      </div>
      <b-button variant="primary" @click="onAddWorkTimeItem">Add Work Item</b-button>

      <b-form-group label="Drive Info" label-for="driveInfo">
        <b-form-input id="driveInfo" v-model="taskObj.driveInfo" type="text"></b-form-input>
      </b-form-group>
      <b-form-group label="Hotel Info" label-for="hotelInfo">
        <b-form-input id="hotelInfo" v-model="taskObj.hotelInfo" type="text"></b-form-input>
      </b-form-group>
      <b-form-group label="Comments" label-for="comments">
        <b-form-input id="comments" v-model="taskObj.comments" type="text"></b-form-input>
      </b-form-group>
      <b-form-group label="Status" label-for="status">
        <b-form-input id="status" v-model="taskObj.status" type="text"></b-form-input>
      </b-form-group>
      <b-form-group label="Assignee" label-for="assignee">
        <b-form-input id="assignee" v-model="taskObj.assignee" type="text"></b-form-input>
      </b-form-group>
      <br />
      <b-button class="mx-auto" type="Save" variant="primary">Save</b-button>
    </b-form>

    <small>{{ taskObj.taskId }}</small>

    <!-- ONLY MODAL -->
    <b-modal size="xl" v-model="showSearchModal" scrollable title="Search">
      <b-container fluid>
        <ArticleSearch v-on:add-item="onAddItemCallback($event)"></ArticleSearch>
      </b-container>

      <template v-slot:modal-footer>
        <div></div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
import router from "../router";
import ArticleSearch from "./ArticleSearch";

export default {
  name: "TaskList",
  props: {
    taskId: {
      type: String,
    
    } 
  },
  components: {
    ArticleSearch
  },
  data() {
    return {
      taskObj: {},

      showSearchModal: false
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();

      ///api/tasks/:id
      const data = {
        task: this.taskObj
      };
      axios
        .post("/api/tasks/" + this.taskId, data)
        .then(response => {
          this.$log.debug("save okay " + response);
          this.$bvToast.toast("Everything is good", {
            title: `Task saved`,
            solid: true
          });
        })
        .catch(errors => {
          this.$log.error("create task fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });
    },

    onAddArticle() {
      this.taskObj.items.push({
        articleId: "",
        description: "",
        amount: 0,
        vendor: ""
      });
    },

    onDeleteArticle(index) {
      this.taskObj.items.splice(index, 1);
    },

    onAddWorkTimeItem() {
      this.taskObj.workTime.push({ worker: "", time: 0 });
    },
    onDeleteWorkTimeItem(index) {
      this.taskObj.workTime.splice(index, 1);
    },

    onAddItemCallback(evt) {
      this.$log.debug(evt);
      this.showSearchModal = false;


      axios
        .get("/api/vendors/" + evt.vendorRef)
        .then(response => {
          this.$log.debug("get vendor ");
          this.$log.debug(response);

          this.taskObj.items.push({
            articleId: evt.articleId,
            description: evt.description1 + evt.description2,
            amount: 1,
            vendor: response.data.vendor.uploadName,
          });
        })
        .catch(errors => {
          this.$log.error("create task fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });
    }

    /*
           articleId: Number,
    description: String,
    amount: Number,

    vendor: String

    */
  },
  mounted() {
    console.log(this.$route.params)
    console.log(JSON.stringify(this.taskId))
    if (this.taskId === "") {
      //no id, no job
      router.push("/");
      return;
    }

    this.$log.debug(this.taskId);
    axios
      .get("api/tasks/" + this.taskId)
      .then(response => {
        this.$log.debug(response);
        this.taskObj = response.data.task;
      })
      .catch(errors => {
        this.$log.error("get task fail " + errors);
        this.$bvToast.toast(errors.message, {
          title: `API Error`,
          variant: "danger",
          solid: true
        });
      });
  }
};
</script>

<style scoped>
#main {
  width: 70%;
  margin: auto;
  text-align: left;
}

.articleId {
  padding: 50px;

}
</style>
